
const Polaroid =({angle,children}) =>(
  <div style={{
    width: "284px",
    padding:" 10px 10px 20px 10px",
    border: "1px solid #BFBFBF",
    backgroundColor: "white",
    backgroundSize:"cover",
    boxShadow: "10px 10px 5px #aaaaaa",
    textAlign: "center",
    transform: `rotate(${angle||7}deg)`,
    objectFit: "cover"
    }}
    id="polaroid"
  >{children}</div>
);

const YtFrame = ({id}) =>(
  <iframe 
    src={`https://www.youtube.com/embed/${id}`}
    width="100%"
  ></iframe>
);

const PolaroidYtGroup = ({data}) => (
  <div id="polaroid-group">{
      data.map((vid,idx) => ( 
        <Polaroid key={idx} angle={idx%2?-8:7}>
          <YtFrame id={vid.ytid} />
          <h6>{vid.title}</h6>
        </Polaroid>
      ))
  }</div>
);

const Videos = ({data}) => {
  return (
    <div>
      <h1>Videos Articles</h1>
      <PolaroidYtGroup {...{data}} />
    </div>
)}

export default Videos;
