import "./SlidingDoor.css";
import {useState} from "react";

const SlidingDoor = () =>{
  const [lock,setLock]= useState(true);
  console.log(lock);
  return (
  <div className="door"
    onClick={()=> setLock(!lock)}
  >
    <div className={`top-door bgimg animate-open ${lock||"top-door-open"}`}></div>
    <div className={`bottom-door bgimg animate-open ${lock||"bottom-door-open"}`}></div>
  </div>
 );
}

export default SlidingDoor;
