import "./Navbar.css";
import {useState} from "react";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import  MenuIcon from "@mui/icons-material/Visibility";
import  HomeIcon from "@mui/icons-material/Home";
import  VidIcon from "@mui/icons-material/VideoLibrary";
import  PermIdentityIcon from "@mui/icons-material/PermIdentity";
import { Outlet, Link } from "react-router-dom";
import Collapse from "react-bootstrap/Collapse";
import Stack from "react-bootstrap/Stack";

const Nav=({children})=>(
<nav className="nav">{children}</nav>
);

const NavToggler=({onClick,children})=>(
  <div className="nav-toggler" {...{onClick}} >
    {children}
  </div>
);

const NavCollapse=({children})=>(
  <div className="nav-collapse"> {children}</div>
);

const NavLink=({link,children})=>(
  <Link className="nav-link" to={link||"/"}>
    {children}
  </Link>
);

const NavBrand=({link,children})=>(
  <a className="nav-brand" href={link||"#"}>
    {children}
  </a>
);

const ToggleButton = ({on}) =>(
  on
  ? <MenuIcon style={{ fontSize: 36 }}/>
  : <MoreVertIcon style={{ fontSize: 36 }}/>
);


