const RoundBtn = ({onClick, children}) => (
  <div
    style={{
    borderRadius:"100%",
    border:"solid 1px black",
    padding:"10px",
    marginBottom:"5%"
    }}
    onClick={onClick}
  >
    {children}
  </div>
);

function Layout(){
  const [closed, setClosed] = useState(true);
  function toggleClosed(){setClosed(!closed)}
  return(
  <div id="layout">
    <div
      id="brand"
    >
    </div>
    <div
      id="navigation-box"
      style={{
        position:"fixed",
        top: "10px",
        right:"20px",
        margin:"0",
	padding: "0",
	zIndex:1
      }}
   >
      <RoundBtn onClick={toggleClosed}>
        <ToggleButton on={closed} />
      </RoundBtn>
      <div
	id="collapse-menu"
	style={{
	  margin:"auto",
	  padding:"auto",
	  display: !closed?"flex":"none",
	  flexDirection:"column",
	  justifyContent:"center",
	}}
      >
	<RoundBtn>  
          <Link to="/">
            <HomeIcon style={{ fontSize: 36 }}/>
          </Link>
	</RoundBtn>
        <RoundBtn>
     	  <Link to="/contacts">
            <PermIdentityIcon
	     style={{ fontSize: 36 }}
	    />
          </Link>
        </RoundBtn>
        <RoundBtn>
     	  <Link to="/videos">
       	    <VidIcon style={{ fontSize: 36 }}/>
          </Link>
        </RoundBtn>
      </div>
    </div>
    <Outlet />
  </div>
)}
export default Layout;
